.section-5 {
    
    height: 100%;
    width: 100%;
    position: relative;

    &-img{
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &-container {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        
    }
    &-content {
        opacity: 0;
        transition: 0.7s ease-in-out;
        transform: translateY(50px);
        margin-bottom: 110px;
        
    }
    &.fadeUppText{
        .section-5{
            &-content {
                opacity: 1 !important;
                transform: translateY(0) !important ;
            }
        }
    }
    .intro-slider{
        &__contnet {
            margin-top: auto;
        }
        &__title{
            color: #fff;
            font-size:  var(--fz-xl);
            margin-bottom: 54px;
            position: relative;
            &::after{
                content: '';
                position: absolute;
                top: -55px;
                left: 0;
                border: 1px solid var(--c-primary);
                width: 80px;
            }            
        }
        &__descr{
            font-size:  var(--fz-m);
            position: relative;
            &:not(:last-child){
                margin-bottom: 26px;
            }

        }
        &__list{
            display: flex;
            position: relative;
        }
        &-item-container {
            z-index: 1;
            display: flex;
        }
        &-item{
            display: flex;
            align-items: center;
            &:not(:last-child){
                margin-right: 50px;
            }
            &.active{
                cursor: pointer;
                background-color: var(--c-secondary);
                width: 300px;
                margin-right: 50px;
                i {
                    display: none;
                }
                p{
                    font-size:  var(--fz-s);
                    font-weight: bold;
                }
            }
            p {
                font-size:  var(--fz-xs);
                font-weight: normal;
            }
        }
        
        &-link{
            &.active{
                padding: 30px 35px;
            }
            display: flex;
            align-items: center;
            column-gap:10px ;
            width: 100%;
           
            i{
                color: var(--c-primary);
            }
            span{
                font-weight: 700;
                transition: color 0.3s ease;
                font-size:  var(--fz-s);
                position: relative;
                text-align: center;
            }
            
        }
        
    }
    
}